* {
    box-sizing: border-box;
    z-index: 100;
}
:root {
    --color-white: #fff;
    --color-violet: #4c328c;
    --color-pink: #E22185;
    --color-blue: #0E4F73;
    --main-font: 'Manrope', sans-serif;
    --main-color: #363F56;
    --main-font-size: 16px;
    --color-grey: #a6a6a6;
}
a {
    text-decoration: none !important;
    color: inherit;
}
button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;

    &:focus {
        outline: none;
    }
}
html {
    background-color: var(--color-white);
}
.wrapper {
    background: url(https://files.doktornarabote.ru//KonstantinGalin/Publications/trigrim_ms/public/main_bg.png) top center no-repeat;
    // position: absolute;
    // top: 0;
    // left: 0;
    // right: 0;
    // bottom: 0;
    overflow-x: hidden;
    // overflow-y: scroll;
    // -webkit-overflow-scrolling: touch;
    background: var(--color-white);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}
body {
    font-family: var(--main-font), sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: var(--main-font-size);
    color: var(--main-color);
    line-height: 1.375;
    letter-spacing: -0.035em;
}
img {
    max-width: 100%;
    display: block;
}
h1,
h2,
h3,
h4,
p {
    margin: 0;
}
//text defaulte
.text {
    &-center {
        text-align: center;
    }
    &-bold {
        font-weight: bold;
    }
    &-right {
        text-align: right;
    }
    &-semi {
        font-weight: 600;
    }
    &-exbold {
        font-weight: 800;
    }
    &-italic {
        font-style: italic;
    }
    &-normal {
        font-weight: 500;
    }
    &-transform {
        text-transform: uppercase;
    }
    &-white {
        color: var(--color-white);
    }
    &-light {
        font-weight: 300;
    }
    &-justify {
        text-align: justify;
    }
}
.text-margin + .text-margin {
    margin-top: 20px;
}
.items-center {
    margin: 0 auto;
}
//grid
.section {
    // padding-top: 3em;
    // padding-bottom: 3em;
    max-width: 100%;
    width: 100%;
    position: relative;

    &._wave {
        &._top {
            padding-top: 6em;
        }
        &._bottom {
            padding-bottom: 6em;
        }
    }
}
.section__content {
    & + .section__content {
        margin-top: 2em;
    }
    .section__content {
        margin-top: 2em;
    }
}
.grid {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
    z-index: 200;
    max-width: 1140px;
}
.grid__row {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
}
//justify-content
.grid__row._lg_content_between {
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.grid__row._lg_content_around {
    justify-content: space-around;
}
.grid__row._lg_content_start {
    justify-content: flex-start;
}
.grid__row._lg_content_end {
    justify-content: flex-end;
}
.grid__row._lg_content_center {
    justify-content: center;
}
//align-items
.grid__row._lg_items_end {
    -webkit-box-align: end;
    -ms-flex-align: end;
    align-items: flex-end;
}
.grid__row._lg_items_start {
    align-items: flex-start;
}
.grid__row._lg_items_center {
    align-items: center;
}
.grid__row._lg_items_stretch {
    align-items: stretch;
}
.grid__col {
    padding-left: 15px;
    padding-right: 15px;

    &._col_flex {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        flex-direction: column;
    }
}
.grid__col._lg_1 {
    width: 8.33333%;
}
.grid__col._lg_2 {
    width: 16.66667%;
}
.grid__col._lg_3 {
    width: 25%;
}
.grid__col._lg_4 {
    width: 33.33333%;
}
.grid__col._lg_5 {
    width: 41.66667%;
}
.grid__col._lg_6 {
    width: 50%;
}
.grid__col._lg_7 {
    width: 58.33333%;
}
.grid__col._lg_8 {
    width: 66.66667%;
}
.grid__col._lg_9 {
    width: 75%;
}
.grid__col._lg_10 {
    width: 83.33333%;
}
.grid__col._lg_11 {
    width: 91.66667%;
}
.grid__col._lg_12 {
    width: 100%;
}
.text-pink {
    color: var(--color-pink);
}
.text-violet {
    color: var(--color-violet);
}
.text-grey {
    color: var(--color-grey);
}
.text-blue {
    color: var(--color-blue);
}
.title {
    //39
    &._big_39 {
        font-size: 2.5em;
        line-height: 1.18em;

        sup {
            font-size: 0.60em;
        }
    }
    //36
    &._big_36 {
        font-size: 2.25em;
        line-height: 1.18em;

        sup {
            font-size: 0.60em;
        }
    }
    //30
    &._big_30 {
        font-size: 1.85em;
        line-height: 1.18em;

        sup {
            font-size: 0.60em;
        }
    }
    //26
    &._ex_lg {
        font-size: 1.25em;
    }
    //24
    &._lg {
        font-size: 1.5em;
        line-height: 1.25;
    }
    //20
    &._md {
        font-size: 1.25em;
        line-height: 1.3;
    }
    //18
    &._sm {
        font-size: 1.125em;
        line-height: 1.388;
    }

    //14
    &._ex_pre_sm {
        font-size: 0.85em;
    }

    //13
    &._ex_sm2 {
        font-size: 0.80em;
    }

    //12
    &._ex_sm {
        font-size: 0.75em;
    }

}

